@use '../../../scss/Foundation/mixin';

.p-article__line-up__slide__wrapper{
    margin-top:calc(40/1440*100vw);
    width:calc(950/1140*100vw);
    position:relative;
    left:calc(120/1440*100vw);
    @include mixin.mq(sp){
        left:calc(18/375*100vw);
        margin-bottom:calc(40/375*100vw);
    }
}