@use '../../../scss/Foundation/mixin';

.p-article__line-up__figure__img{
    object-fit: cover;
    overflow: hidden;
    position:relative;
    z-index: 0;
    /*
    display: block;
    &:hover{
    transition: 0.8s;
    opacity:(0.4);
    transform: scale(1.1, 1.1);
    */
    @include mixin.mq(sp){
        width:calc(340/375*100vw);
        height:calc(250/375*100vw);
        
    }
    
}
.p-article__line-up__figure__img.is-hover{
    transition: 0.8s;
    opacity:(0.4);
    transform: scale(1.1, 1.1);
}