@use '../../Foundation/mixin';

.p-article__bottom-contents__heading2{
    font-family: '',meiryo,' pro w3','hiragino kaku gothic pro',sans-serif;
    color:#F7004E;
    font-size:calc(32/1440*100vw);
    font-weight: 600;
    letter-spacing: .2rem;
    //text-align: center;
    @include mixin.mq(tb){
        text-align: center;
        font-size:calc(24/1023*100vw);
        margin-bottom:calc(24/1023*100vw);
    }
    @include mixin.mq(sp){
        margin-bottom:calc(24/375*100vw);
        font-size:calc(24/375*100vw);
    }
}