@use '../../../scss/Foundation/mixin';
.p-scroll__pick-up{
    position:relative;
    order: 4;
    color:#fafafa;
    margin-top: calc(20/1440*100vw);
    font-size: calc(20/1440*100vw);
    font-weight: 600;
    font-family:tsukushioldmincho,ms mincho, pro w3,hiragino mincho pro,serif;
    &:after{
        display:block;
        content:"";
        position:absolute;
        width:calc(4/1440*100vw);
        //
        min-height:100rem;
        background-color: #fafafa;
        top: calc(40/1440*100vw);
        left:calc(35/1440*100vw);
        @include mixin.mq(sp){
            left:calc(20/375*100vw);
        }
    }
}