@use '../../../scss/Foundation/mixin';

.p-article__quality-support__heat-resistant__figcaption{
    width:calc(180/1440*100vw);
    margin:0 auto;
    text-align: center;
    margin-top:calc(50/1440*100vw);
    font-size:max(calc(14/1440*100vw),14px);
    font-weight: 600;
    color:#fffffe;
    background-color:#ff4040;
    padding:0 calc(8/1440*100vw);
    font-family: tsukushigothic,ms gothic, pro w3,hiragino kaku gothic pro,osaka,sans-serif;
    @include mixin.mq(sp){
        width:calc(150/375*100vw);
    }
}